const initialState = {
  status: 0, // 0 - Unlocked, 1 - Locked by me, 2 - Locked by someone else, 3 - Takeover attempt
  lockId: undefined,
  description: '',
  user: '',
  host: '',
  takeoverId: undefined,
  takeoverMsg: '',
  takeoverUser: '',
  takeoverHost: '',
  releaseId: undefined,
  releaseReason: '',
};

const LockStateReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'RESET_LOCK':
      return {
        ...state,
        status: 0,
      };
    case 'SET_LOCK_RELEASED':
      return {
        ...state,
        status: 0,
        releaseId: action.id,
        releaseReason: action.reason,
      };
    case 'SET_LOCK_ACQUIRED':
      return {
        ...state,
        status: 1,
        lockId: action.id,
        description: action.description,
        user: action.user,
        host: action.host,
      };
    case 'SET_LOCK_REACQUIRED':
      return {
        ...state,
        status: 1,
      };
    case 'SET_LOCK_ALREADY_LOCKED':
      return {
        ...state,
        status: 2,
        lockId: action.id,
        description: action.description,
        user: action.user,
        host: action.host,
      };
    case 'SET_LOCK_TAKEOVER':
      return {
        ...state,
        status: 3,
        takeoverId: action.id,
        takeoverMsg: action.message,
        takeoverUser: action.user,
        takeoverHost: action.host,
      };
    default:
      return state;
  }
};

export default LockStateReducer;
