import React, { useReducer, useState } from 'react';
import { useSelector } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import InputAdornment from '@material-ui/core/InputAdornment';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';

import lockIcon from '../../../../../src/assets/icons/icons8-lock.svg'
import padlockIcon from '../../../../../src/assets/icons/icons8-padlock.svg'

import AlertDialog from './alertDialog';

import './style.scss';

const formReducer = (state, event) => {
  return {
    ...state,
    [event.name]: event.value
  }
}

const LockIcon = () => {
  return (
    <img src={lockIcon} alt="Lock icon"/>
  );
}

const PadlockIcon = () => {
  return (
    <img src={padlockIcon} alt="Padlock icon"/>
  );
}

const LockStateHandler = () => {
  const proxy = useSelector((state) => state.app.proxy);
  const lockState = useSelector((state) => state.lockState);
  const [openFormDialog, setOpenFormDialog] = useState(false);
  const [formData, setFormData] = useReducer(formReducer, {'scope': 1, 'requestTakeover': false});

  const isLockedByMeStatus = () => {
    return lockState.status === 1;
  }

  const isLockAlreadyLockedStatus = () => {
    return lockState.status === 2;
  }

  const isTakeoverAttemptStatus = () => {
    return lockState.status === 3;
  }

  const handleChange = (event) => {
    const isCheckbox = event.target.type === 'checkbox';
    setFormData({
      name: event.target.name,
      value: isCheckbox ? event.target.checked : event.target.value,
    });
  }

  const handleClick = () => {
    if (isLockedByMeStatus()) {
      proxy.unsubscribeFromLockAcquisitionStream();
      const explicitReleaseNotification = {'lockId': lockState.lockId, 'reason': 'ExplicitRelease'};
      proxy.onLockReleasedNotification(explicitReleaseNotification);
    }
    else {
      setOpenFormDialog(true);
    }
  }

  const handleClose = () => {
    setOpenFormDialog(false);
    resetTimeouts();
  }

  const handleSubmit = (event) => {
    event.preventDefault();
    setOpenFormDialog(false);
    proxy.subscribeToLockAcquisitionStream(formData);
    resetTimeouts();
  }

  const resetTimeouts = () => {
    setFormData({
      name: 'lockExpire',
      value: 0,
    });
    setFormData({
      name: 'takeoverTimeout',
      value: 0,
    });
  }

  const renderAlertDialogs = () => {
    return (
      <>
        <AlertDialog
          open={isLockAlreadyLockedStatus()}
          title={<FormattedMessage
            id="lockStateHandler.alertDialog.lockAlreadyLocked"
            values={{user: lockState.user}} />}
          contentText={<FormattedMessage
            id="lockStateHandler.alertDialog.lockDescription"
            values={{description: lockState.description}} />}
        >
          <Button
            onClick={() => proxy.onLockReset()}
            color="primary"
          >
            <FormattedMessage id="close" />
          </Button>
        </AlertDialog>
        <AlertDialog
          open={isTakeoverAttemptStatus()}
          title={<FormattedMessage
            id="lockStateHandler.alertDialog.takeoverAttempt"
            values={{user: lockState.takeoverUser}} />}
          contentText={<FormattedMessage
            id="lockStateHandler.alertDialog.takeoverMessage"
            values={{message: lockState.takeoverMsg}} />}
        >
          <Button
            onClick={() => {
              proxy.passConfigLock({
                'lockId': lockState.lockId,
                'takeoverId': lockState.takeoverId,
              });
            }}
            color="primary"
          >
            <FormattedMessage id="agree" />
          </Button>
          <Button
            onClick={() => proxy.onLockReacquired()}
            color="primary"
          >
            <FormattedMessage id="disagree" />
          </Button>
        </AlertDialog>
      </>
    );
  }

  const renderFormDialog = () => {
    return (
      <Dialog open={openFormDialog} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">
          <FormattedMessage id="lockStateHandler.formDialog.lockConfigRequestTitle" />
        </DialogTitle>
        <DialogContent>
          <DialogContentText>
            <FormattedMessage id="lockStateHandler.formDialog.lockConfigRequestContent" />
          </DialogContentText>
          <TextField
            InputLabelProps={{ shrink: true }}
            autoFocus
            margin="dense"
            id="lock-scope-id"
            name="scope"
            label={<FormattedMessage id="lockStateHandler.formDialog.lockScope" />}
            type="number"
            defaultValue="1"
            onChange={handleChange}
            fullWidth
          />
          <TextField
            InputLabelProps={{ shrink: true }}
            margin="dense"
            id="lock-timeout-id"
            name="lockExpire"
            label={<FormattedMessage id="lockStateHandler.formDialog.lockTimeout" />}
            type="number"
            InputProps={{
              endAdornment: <InputAdornment position="end">milliseconds</InputAdornment>,
            }}
            defaultValue="0"
            onChange={handleChange}
            fullWidth
          />
          <TextField
            InputLabelProps={{ shrink: true }}
            margin="dense"
            id="lock-description-id"
            name="description"
            label={<FormattedMessage id="lockStateHandler.formDialog.lockDescription" />}
            type="text"
            onChange={handleChange}
            fullWidth
          />
          <FormControlLabel
            margin="dense"
            label={<FormattedMessage id="lockStateHandler.formDialog.takeoverRequest" />}
            control={
              <Checkbox
                onChange={handleChange}
                checked={formData.requestTakeover}
                id="takeover-request-id"
                name="requestTakeover"
                color="primary"
              />
            }
          />
          {formData.requestTakeover &&
            <>
              <TextField
                InputLabelProps={{ shrink: true }}
                margin="dense"
                id="takeover-timeout-id"
                name="takeoverTimeout"
                label={<FormattedMessage id="lockStateHandler.formDialog.takeoverTimeout" />}
                type="number"
                InputProps={{
                  endAdornment: <InputAdornment position="end"><FormattedMessage id="milliseconds" /></InputAdornment>,
                }}
                defaultValue="0"
                onChange={handleChange}
                fullWidth
              />
              <TextField
                InputLabelProps={{ shrink: true }}
                margin="dense"
                id="takeover-message-id"
                name="takeoverMessage"
                label={<FormattedMessage id="lockStateHandler.formDialog.takeoverMessage" />}
                type="text"
                onChange={handleChange}
                fullWidth
              />
            </>
          }
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            <FormattedMessage id="cancel" />
          </Button>
          <Button onClick={handleSubmit} color="primary">
            <FormattedMessage id="submit" />
          </Button>
        </DialogActions>
      </Dialog>
    );
  }

  return (
    <div className="lock-state-handler-wrapper">
      <Button
        className="lock-button"
        onClick={handleClick}
        startIcon={isLockedByMeStatus() ? <PadlockIcon /> : <LockIcon />}
      >
        {isLockedByMeStatus() ? <FormattedMessage id="topNavigation.release"/>
                              : <FormattedMessage id="topNavigation.lock"/>}
      </Button>
      {renderFormDialog()}
      {renderAlertDialogs()}
    </div>
  );
}

export default LockStateHandler;
